import axios from 'axios'
import React, { useEffect, useState } from 'react'
import './App.css'
import MovieUi from './MovieUi'
const App = () => {
  const [movies,setMovies]=useState([])
  const [filterm,setFilterm]=useState([])
  useEffect(() => {
  fetch('https://www.omdbapi.com/?apikey=45f0782a&s=war').then(result=>result.json()).then(data=>{setMovies(data.Search)
  setFilterm(data.Search)})

  }, [])
  const change=(e)=>{
    const filter=filterm.filter((data)=>{return data.Title.toLowerCase().includes(e.target.value.toLowerCase())})
    setMovies(filter)
console.log(e.target.value)
  }
  return (
<>
<div className="search">
  <input  onChange={change} type="search" name="movie" id="" placeholder="Search Movie here" />
  </div>
<div className="container">
<div className="movie_card">
{movies.map((data,key)=>{
  console.log(data)
  return(

  <MovieUi key={data.imdbID} img={data.Poster} title={data.Title} year={data.Year} imdbId={`https://www.imdb.com/title/${data.imdbID}`}/>
  )
})}
</div>
</div>
</>

  )
}

export default App
